import React, {useState, useEffect} from "react";
import {withRouter, Link } from "react-router-dom";
import Hamburger from "./Hamburger";


const Header = ({history}) => {

 const [open, setopen] = useState({
     initial:false,
     clicked:false,
     name:'Menu'    
 })

 const [disables, setdisables] = useState(false); 

 const handleOpen = () => {
     disableMenu()
     if(open.initial === false){
         setopen({
             initial:null,
             clicked:false,
             name:"Close"   
         })
         console.log(1);
     }else if(open.clicked === true){
        setopen({
             clicked:!open.clicked,
             name:"Menu"
         })
         console.log(2);
     }else if(open.clicked === false){
        setopen({
             clicked:!open.clicked,
             name:"Close"
         })
         console.log(3);
     }

 }

 const disableMenu = () => {
     setdisables(!disables)
     setTimeout(() => {
         setdisables(false)
     }, 1000)
 }


 useEffect(() => {
    history.listen(() => {
        setopen({
            clicked:false,
            name:"Menu"
        })
    })
 }, [])




  return (
    <header>
        <div className="container">
            <div className="wrapper">
                <div className="inner-header">
                    <div className="logo">
                        <Link to='/'>Sunapana.</Link>
                    </div>
                    <div className="menu">
                        <button disabled={disables} onClick={handleOpen}>{open.name}.</button>
                    </div>
                </div>
            </div>
        </div>
        <Hamburger open={open}/>
    </header>
  );
};

export default withRouter(Header)