import React, {useEffect, useRef} from "react";
import { Link } from "react-router-dom";
// import gsap from "gsap";
import { Power2, TimelineLite} from "gsap/gsap-core";

const Hamburger = ({open }) => {

    let menu = useRef(null)
    let revealMenu = useRef(null)
    let revealMenubg = useRef(null)
    let cityBg = useRef(null)
    let line1 = useRef(null)
    let line2 = useRef(null)
    let line3 = useRef(null)

    const t1 = new TimelineLite()

    useEffect(() => {
        if(open.clicked===false){
            menu.style.display = "none"
            
        t1.to(revealMenu.current, {
                duration: 0.8,
                height: 0,
                ease:Power2.easeInOut
            }).to(menu , {
                duration: 1,
                css:{display:'none'}
            })


        }else if(open.clicked===false && open.initial === null || open.clicked === true){
            menu.style.display = "block"
        }
    }, [open])


  return(
      <div ref={e => (menu = e)} className="hambuger-menu">
          <div ref={e => {revealMenubg = e}} className="menu-second-bg-color"></div>
          <div ref={e => (revealMenu = e)} className="menu-layer">
              <div className="menu-city-bg">

              </div>
              <div className="container">
                  <div className="wrapper">
                      <div className="menu-links">
                          <nav>
                              <ul>
                                  <li>
                                      <Link ref={e => (line1 = e)} to='/opportunities'>Oppurtunities</Link>
                                  </li>
                                  <li>
                                      <Link ref={e => (line2 = e)} to='/solutions'>Solutions</Link>
                                  </li>
                                  <li>
                                      <Link ref={e => (line3 = e)} to='/contact-us'>Contact</Link>
                                  </li>
                              </ul>
                          </nav>
                          <div className="locations">
                              Locations : 
                              <span> Spain </span>
                              <span>Amsterdam </span>
                              <span>Netherland </span>
                              <span>Hawkins </span>
                          </div>
                      </div>
                  </div>
              </div>
          </div>
      </div>
  );
};

export default Hamburger;